
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'

import goodsItem from './components/goodsItem'
import http from '@/http'

export default {
components: {
TabItem,
Tab,
XHeader,
'good-item': goodsItem,
Timeline,
TimelineItem
},
methods: {
getExpress () {
http('ht-mj-order-server/order/wx/logisticsDetail', {orderId: this.$route.params.Id}).then((res) => {
this.list = res
this.list = this.list.map((v, index) => {
v.merchantId = index
v.index = index
v.wxOrderGoodsVOList = v.orderGoodsVOList.map((v) => {
return {
goodsName: v.goodsName,
mainImgUrl: v.mainImgUrl,
goodsType: v.goodsType,
propertyValue: v.propertyValue,
goodsNum: v.goodsNum
}
})
return v
})
this.data = res[0]
this.currentIndex = 0
this.tab = this.list.map((v, index) => {
return '包裹' + (index + 1)
})
})
},
aa (index) {
if (index !== this.currentIndex) {
this.data = this.list[index]
this.currentIndex = index

this.hackReset = false
this.$nextTick(() => {
this.hackReset = true
})
}
}
},
mounted: function () {
this.getExpress()
},
data () {
return {
data: undefined,
hackReset: true,
tabIndex: 0,
currentIndex: 0,
list: [],
tab: []
}
}
}



